* {
    margin: 0;
    padding: 0;
    font-family: "\5FAE\8F6F\96C5\9ED1";
    font-size: 62.5%;
    border: none;
	outline: none;
}


a,ul,li {
    list-style: none;
    text-decoration: none;
    display: block;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.content {
    width: 100%;
}

.clear {
    clear: both;
}

@font-face {
    font-family: myFirstFont;
    src: url(/fonts/simkai.ttf) format("truetype"),
         url(/fonts/simkai.eot) format('eot'),
         url(/images/simkai.svg) format('svg'),
         url(/fonts/simkai.woff) format('woff');
}
